import React from 'react';
import {Wrapper} from "./StyledWrapper"


const InfoCard = ({obj}) => {
  console.log(obj);

  return (
    <Wrapper>
      <h3 className="title">INFO</h3>
      <p>
        <b>xona_soni</b> {obj.xona_soni}
      </p>
      <p>
        <b>umumiy_kv</b>: {obj.umumiy_kv}
      </p>
      <p>
        <b>prixoshka</b>: {obj.prixoshka}
      </p>
      <p>
        <b>xojatxona_1</b>: {obj.xojatxona_1}
      </p>
      <p>
        <b>xojatxona_2</b>: {obj.xojatxona_2}
      </p>
      <p>
        <b>vanna_1</b>: {obj.vanna_1}
      </p>
      <p>
        <b>vanna_2</b>: {obj.vanna_2}
      </p>
      <p>
        <b>spalniy</b>: {obj.spalniy}
      </p>
      <p>
        <b>detskiy</b> {obj.detskiy}
      </p>
      <p>
        <b>kuxniya</b>: {obj.kuxniya}
      </p>
      <p>
        <b>status</b>: {obj.status}
      </p>

    </Wrapper>
  );
};

export default InfoCard;
