import React, {useLayoutEffect, useRef} from 'react';
import {Button, Form, Input} from "antd";
import {Wrapper} from "./StyledWrapper";
import axios from "axios";
import {MainApi} from "../../../api";
import {toast} from "react-toastify";

const EditCard = ({obj}) => {
  const formRef = useRef(null);

  const onSave = (values) => {
    const body = {
      ...obj,
      ...values
    };
    console.log(body);

    axios.put(`${MainApi}/api/honadon/update/${obj._id}`, body)
      .then((res) => {
        console.log(res);
        toast.success("Muvaffaqqiyatli tahrirlandi!");
      })
      .catch(err => {
        console.log(err);
        toast.error("Error!");
      })
  }

  useLayoutEffect(() => {
    formRef.current.setFieldsValue({...obj});
  }, [])

  return (
    <Wrapper>
      <h3 className="title">EDIT</h3>
      <Form
        onFinish={onSave}
        className="inputs-wrapper"
        autoComplete="off"
        size="small"
        ref={formRef}
      >
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>xona_soni:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="xona_soni"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>umumiy_kv:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="umumiy_kv"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>prixoshka:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="prixoshka"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>xojatxona_1:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="xojatxona_1"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>xojatxona_2:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="xojatxona_2"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>vanna_1:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="vanna_1"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>vanna_2:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="vanna_2"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>spalniy:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="spalniy"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>detskiy:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="detskiy"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>kuxniya:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="kuxniya"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        <div className="input-wrapper">
          <b>status:</b>
          <Form.Item
            rules={[{required: true, message: '*'}]}
            name="status"
          >
            <Input/>
          </Form.Item>
        </div>
        {/*-----------INPUT------------*/}
        {/*<div className="input-wrapper">*/}
        {/*  <b>Yotohona:</b>*/}
        {/*  <Form.Item*/}
        {/*    rules={[{required: true, message: '*'}]}*/}
        {/*    name="Yotohona"*/}
        {/*  >*/}
        {/*    <Input/>*/}
        {/*  </Form.Item>*/}
        {/*</div>*/}


        <Button type="primary" htmlType={"submit"}>
          Saqlash
        </Button>
      </Form>
    </Wrapper>
  );
};

export default EditCard;
