import React, {useEffect, useState} from 'react';
import {Tabs} from "src/components";
import {PlusCircleOutlined} from "@ant-design/icons";
import RoomPage from "src/components/room/room";
import {useNavigate, useParams} from "react-router-dom";
import axios from "axios";
import {MainApi} from "../api";


function Uy() {
  const [index, setIndex] = useState(0)
  const [data, setData] = useState(false);
  const navigate = useNavigate();

  const {houseId} = useParams();

  const handleDelete = () => {
    navigate(`/create-room/${houseId}`);
  }

  useEffect(() => {
    axios.get(`${MainApi}/flat?category=${houseId}`)
        .then(res => {
          console.log(res.data.data);
          setData(!!res.data.data.length);
          console.log("HONADONLAR", res.data);
        })
        .catch(err => {
          console.log(err);
        });
  },[])

  return (
    <>
      <Tabs
        index={index}
        setIndex={setIndex}
        tabs={[
          {
            title: "Xonadonlar",
            content: (
              <RoomPage/>
            ),
          },
        ]}
        tabRightContent={data ? [] : [
          {
            icon: <PlusCircleOutlined style={{fontSize: "24px"}}/>,
            onClick: () => handleDelete(),
          },
        ]}
      />
    </>

  );
}

export default Uy;
