import styled, {css} from "styled-components";

export const StyledCard = styled.div`
  background-color: ${props => props.differentBg ? "#333" : "#fafafa"};
  color: ${props => props.differentBg ? "#fff" : "#000"};
  border: 1px solid #c1c1c1;
  width: 100%;
  text-decoration: none;
  cursor: pointer;
  min-height: 150px;
  height: 100%;

  .card_header {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #c1c1c1;
    margin-bottom: 10px;
    background-color: #ededed;

    &__title-link {
      color: ${props => props.differentBg ? "#fff" : "#000"};
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }

    .actions {
      .info {
        font-size: 15px;
        color: #0bb900;
        margin-right: 10px;
        transition: transform 0.2s ease;

        &:hover {
          transform: scale(1.125);
        }
      }

      .edit {
        font-size: 15px;
        color: #0070f3;
        margin-right: 10px;
        transition: transform 0.2s ease;

        &:hover {
          transform: scale(1.125);
        }
      }

      .delete {
        font-size: 15px;
        color: red;
        transition: transform 0.2s ease;

        &:hover {
          transform: scale(1.125);
        }
      }
    }
  }

  &:hover {
    border: 1px solid #595959;
  }

  h3 {
    text-align: center;
    font-weight: bolder;
    text-transform: uppercase;
    padding: 15px 5px;
    color: ${props => props.differentBg ? "#fff" : "#000"};
    margin: 0;
  }

  p {
    margin: 0;
  }

  ${props => {
    let bg = "";
    let color = "#000";
    switch (props.status) {
      case 0: {
        bg = "#43ff38";
        color = "#fff";
        break;
      }
      case 1: {
        bg = "#dbdb1b";
        color = "#fff";
        break;
      }
      case 2: {
        bg = "#fc1919";
        color = "#fff";
        break;
      }
    }
    return `
      background-color: ${bg};
      color: ${color};
    `
  }}

`;
