import {Col, Modal, Row} from "antd";
import React, {useEffect, useState} from "react";
import axios from "axios";
import {DeleteOutlined, EditOutlined, ExclamationCircleOutlined, InfoCircleOutlined} from "@ant-design/icons";
import {MainApi} from "../../api";
import {toast} from "react-toastify";
import {Link, useParams} from "react-router-dom";
import {StyledCard} from "./StyledCard";
import InfoCard from "./InfoCard";
import EditCard from "./EditCard";

function RoomPage() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalType, setModalType] = useState("INFO");
  const [activeItem, setActiveItem] = useState(false);
  const [data, setData] = useState([]);
  const [contractData, setContractData] = useState([]);
  const {houseId} = useParams();

  const handleDelete = (id) => {
    Modal.confirm({
      centered: true,
      title: "Rostan ham o'chirmoqchimisiz?",
      icon: <ExclamationCircleOutlined/>,
      onOk() {
        axios.delete(`${MainApi}/flat/${id}`).then(res => {
          console.log("DELETE RESPONSE", res);
          toast.success("Muvaffaqqiyatli o'chirildi!")
          getData()
        }).catch(er => toast.error("Error"))
      },
    })
  }

  const handleEdit = (item) => {
    setActiveItem(item);
    setModalType("EDIT");
    setIsModalOpen(true);
  }

  const handleInfo = (item) => {
    setActiveItem(item);
    setModalType("INFO");
    setIsModalOpen(true);
  }

  useEffect(() => {
      axios.get(`${MainApi}/shartnoma`)
          .then(res => {
              setContractData(res.data.data);
          })
          .catch(err => {
              console.log(err);
          })
  },[])

  const getData = () => {
    setTimeout(() => {
      axios.get(`${MainApi}/flat?category=${houseId}`)
        .then(res => {
          console.log(res.data.data);
          setData(res.data.data);
          console.log("HONADONLAR", res.data);
        })
        .catch(err => {
          console.log(err);
        });
    })
  }

  useEffect(() => {
    getData()
  }, []);

  let temp = 0;
    console.log('contractData',contractData)
  return (
    <div>
      <Row>
        {data.map((v, i) => {
          console.log(v?.category?.name)

          return (
            <>
              {((i+1+temp) % 6 === 1 ) && <Col span={4} key={i}>
                <StyledCard>
                  <div style={{height: "100%", width: "100%", display: "flex", alignItems: "center", justifyContent: "center"}}>
                    <span style={{fontSize: 24}}>
                      {++temp}-ETAJ
                    </span>
                  </div>
                </StyledCard>
              </Col>}
              <Col span={4} key={i}>
                <StyledCard status={contractData.status}>
                  <div className="card_header">
                    <div>
                      <Link className="card_header__title-link" to={`/Create-contract/${v._id}`}>
                        <p>Shartnoma</p>
                      </Link>
                    </div>
                    <div className="actions">
                      <InfoCircleOutlined className="info" onClick={e => {
                        e.stopPropagation();
                        handleInfo(v);
                      }}/>
                      <EditOutlined className="edit" onClick={(e) => {
                        e.stopPropagation();
                        handleEdit(v);
                      }}/>
                      <DeleteOutlined className="delete" onClick={(e) => {
                        e.stopPropagation();
                        handleDelete(v._id);
                      }}/>
                    </div>
                  </div>
                  <h3>{i + 1}. {v.Nomi}</h3>
                </StyledCard>
              </Col>
            </>
          )
        })}
      </Row>
      <Modal
        open={isModalOpen}
        footer={null}
        onCancel={() => setIsModalOpen(false)}
        title={(
          <div className="modalCardTitleActions">
            <InfoCircleOutlined className="info" onClick={e => {
              e.stopPropagation();
              setModalType("INFO");
            }}/>
            <EditOutlined className="edit" onClick={(e) => {
              e.stopPropagation();
              setModalType("EDIT");
            }}/>
          </div>
        )}
      >
        {modalType === "INFO" && <InfoCard
          obj={activeItem}
        />}
        {modalType === "EDIT" && <EditCard
          obj={activeItem}
        />}
      </Modal>
    </div>
  );
}

export default RoomPage;
